.dropbtn {
  outline: 1px solid var(--primary-clr-06);
  background-color: var(--primary-clr-04);
  color: white;

  padding: 4px 10px;
  border: none;
  cursor: pointer;
}
.dropbtn:hover {
  background-color: var(--primary-clr-05);
  color: var(--primary-clr-04);
}
.fa-caret-down {
  margin-left: 1em;
  font-size: 1.25em;
}
.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  min-width: 120px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.dropdown-content a:nth-child(odd) {
  background-color: #5134f1;
}
.dropdown-content a {
  color: black;
  padding: 4px 10px;
  border: 1px solid white;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  background-color: var(--primary-clr-07);
  border: none;
}

.dropdown:hover .dropdown-content {
  display: block;
}

#politie-logo {
  height: 100px;
  width: 100px;
  top: 0em;
  position: relative;
}
.navigation {
  display: flex;
  justify-content: space-evenly;
  background-color: var(--primary-clr-04);
  padding: 1em;
  height: 50px;
  position: fixed;
  width: 100%;
  top: 0;
  box-shadow: var(--grad-clr-02);
  width: 100%;
}

.navigation a::first-letter {
  color: var(--primary-clr-06);
}
.navigation a {
  text-decoration: none;
  color: white;
  font-size: 14px;
  padding: 4px 10px;
  border: 1px solid var(--primary-clr-06);
}
.navigation a:hover {
  border-bottom: solid 3px var(--primary-clr-06);
}
.nav-left,
.nav-right {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
