@import url(https://fonts.googleapis.com/css2?family=Abel&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ephesis&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: "Roboto";
  margin: 0px 40px 20px;
  background-image: linear-gradient(
    to right,
    rgba(202, 205, 245, 0.24),
    rgb(151, 191, 236)
  );
}

#filters-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 40px;
  color: white;
  border: 2px rgb(168, 175, 249) solid;
  background-image: var(--grad-clr-03);
  font: bold 1em Roboto;
}

label {
  margin: 0px 10px;
  font-weight: bold;
  white-space: nowrap;
  padding: 8px 5px;
}

.chart-div {
  display: flex;
  background-image: var(--grad-clr-03);
  padding: 12px;
  border: 2px rgb(2, 12, 119) solid;
}

figure {
  /* margin: 0; */
  margin-bottom: 20px;
  border: 2px solid rgb(241, 241, 246);
  outline: 6px solid darkblue;
  max-width: 100%;
  padding: 40px;
  background-color: var(--primary-clr-04);
}

@media (max-width: 768px) {
  .gallery-container {
    display: flex;
    flex-wrap: wrap;
  }
}

footer {
  background-color: rgb(13, 13, 13);
  color: white;
  padding: 25px;
  margin: 2em auto;
}

footer div a {
  color: var(--primary-clr-03);
  text-decoration: none;
}

.vacatures {
  display: flex;
  justify-content: center;
  grid-gap: 1em;
  gap: 1em;
}
.disclaim {
  display: flex;
  justify-content: center;
  grid-gap: 1em;
  gap: 1em;
  padding: 8px 0;
}
.disclaim a {
  color: var(--primary-clr-01);
  font-size: 0.75em;
  border-right: 2px solid var(--primary-clr-01);
  padding-right: 10px;
}
.disclaim a:last-child {
  border: none;
}
.disclaim a:hover {
  border-bottom: 2px solid white;
}

.vacatures a:hover {
  border-bottom: 2px var(--primary-clr-06) solid;
}
.social-media {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5em;
  padding: 1em;
  grid-gap: 0.5em;
  gap: 0.5em;
}
.social-media a {
  color: #fff;
}
.social-media .fa:hover {
  cursor: pointer;
  padding: 0.5em;
  border-radius: 50%;
  border: white 1.5px solid;
}

#checkbox-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  /* margin: 8px; */
  /* gap: 0.5em; */
}

#aangifte-checkbox-label {
  color: aliceblue;
  background-color: var(--primary-clr-04);
}

#gemist-checkbox-label {
  color: var(--primary-clr-04);
  background-color: var(--primary-clr-06);
}

#gemist-checkbox-label,
#aangifte-checkbox-label {
  padding: 5px 10px;
  font: 0.8em Roboto;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-transform: uppercase;
}

#gemist-checkbox-label:hover {
  background-color: var(--primary-clr-07);
  color: var(--primary-clr-04);
}

#aangifte-checkbox-label:hover {
  background-color: var(--primary-clr-07);
  color: var(--primary-clr-04);
}

body {
  display: flex;
  justify-content: center;
  margin: 0;
  box-sizing: border;
}

#select-provincie {
  border: 1px solid white;
  padding: 0px 10px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  font: 0.8em Roboto;
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
  letter-spacing: 4px;
  background-color: rgb(192, 249, 249);
}

.fa-caret-left {
  font-size: 2em;
  margin-right: 6px;
}
nav {
  display: flex;
  justify-content: center;
}
.dropdown-menu {
  position: relative;
  display: flex;
  justify-content: center;
  margin: 1em;
  color: darkblue;
}

.dropdown-menu-content {
  display: none;
  position: absolute;
  padding-right: 18px;
  margin-top: 9px;
  z-index: 1;
  border: 1px solid white;
  border-radius: 6px;
  font-size: 1em;
  width: 200px;
}

.dropdown-menu:hover .dropdown-menu-content {
  display: block;
  background-color: rgb(24, 4, 57);
  font-weight: bold;
  margin-top: 2em;
  color: white;
}
ul a {
  display: block;
  color: white;
  text-decoration: none;
  padding-left: 18px;
}

li {
  background-color: var(--primary-clr-03);
  text-align: left;
  margin: 6px;
  list-style-type: none;
}

ul li:hover {
  padding: 4px;
  background-color: var(--primary-clr-06);
}

#provincie-naam {
  font: 1em Roboto;
  padding: 4px 20px;
  border: 1px solid white;
  white-space: nowrap;
  color: var(--primary-clr-04);
  background-color: aqua;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
}

.dropbtn {
  outline: 1px solid var(--primary-clr-06);
  background-color: var(--primary-clr-04);
  color: white;

  padding: 4px 10px;
  border: none;
  cursor: pointer;
}
.dropbtn:hover {
  background-color: var(--primary-clr-05);
  color: var(--primary-clr-04);
}
.fa-caret-down {
  margin-left: 1em;
  font-size: 1.25em;
}
.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  min-width: 120px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.dropdown-content a:nth-child(odd) {
  background-color: #5134f1;
}
.dropdown-content a {
  color: black;
  padding: 4px 10px;
  border: 1px solid white;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  background-color: var(--primary-clr-07);
  border: none;
}

.dropdown:hover .dropdown-content {
  display: block;
}

#politie-logo {
  height: 100px;
  width: 100px;
  top: 0em;
  position: relative;
}
.navigation {
  display: flex;
  justify-content: space-evenly;
  background-color: var(--primary-clr-04);
  padding: 1em;
  height: 50px;
  position: fixed;
  width: 100%;
  top: 0;
  box-shadow: var(--grad-clr-02);
  width: 100%;
}

.navigation a::first-letter {
  color: var(--primary-clr-06);
}
.navigation a {
  text-decoration: none;
  color: white;
  font-size: 14px;
  padding: 4px 10px;
  border: 1px solid var(--primary-clr-06);
}
.navigation a:hover {
  border-bottom: solid 3px var(--primary-clr-06);
}
.nav-left,
.nav-right {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

:root {
  --primary-clr-01: aliceblue;
  --primary-clr-02: #bfbdbd;
  --primary-clr-03: rgb(24, 104, 201);
  --primary-clr-03-a: rgb(97, 137, 185);
  --primary-clr-04: rgba(9, 12, 81, 0.981);
  --primary-clr-05: rgb(245, 245, 33);
  --primary-clr-06: rgb(251, 203, 82);
  --primary-clr-07: rgb(161, 243, 67);

  /* gradient colors */

  --grad-clr-01: linear-gradient(
    to right,
    rgba(202, 205, 245, 0.24),
    rgb(151, 191, 236)
  );

  --grad-clr-02: rgba(0, 0, 0, 0.25) 0px 54px 55px,
    rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px,
    rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;

  --grad-clr-03: linear-gradient(
    to right,
    rgba(202, 205, 245, 0.24),
    rgb(100, 162, 232)
  );

  --grad-clr-04: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(89, 128, 241, 1) 0%,
    rgba(0, 212, 255, 1) 100%
  );
}

