.morgen {
  /* border: red 1px solid; */
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 1em;
  margin-top: 4em;
  width: 100%;
}
.zoek {
  width: 100%;
  border-radius: 4px;
  border: 1px solid blue;
  padding: 10px;
  margin-top: 1em;
}
.zoek:hover {
  border: 1.5px solid red;
}
.spoed {
  display: block;
  font-size: 1.12em;
  padding: 8px;
  margin: 8px 0;
  -webkit-animation-name: bellen;
          animation-name: bellen;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

@-webkit-keyframes bellen {
  25% {
    background-color: var(--primary-clr-06);
    color: red;
  }
  100% {
    background-color: var(--primary-clr-04);
    color: white;
  }
}

@keyframes bellen {
  25% {
    background-color: var(--primary-clr-06);
    color: red;
  }
  100% {
    background-color: var(--primary-clr-04);
    color: white;
  }
}
main h1 {
  font-size: 1.5em;
  padding: 16px;
  background-color: var(--primary-clr-07);
  color: var(--primary-clr-04);
}
