h3 {
  color: var(--primary-clr-04);
}
p i {
  margin-left: 1em;
}
.article {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  grid-column-gap: 2em;
  -webkit-column-gap: 2em;
          column-gap: 2em;
}
.article p {
  background-color: var(--primary-clr-01);
  padding: 1.6em;
  width: 320px;
  /* font-size: 1.25em; */
  box-shadow: var(--grad-clr-02);
}
.article a {
  text-decoration: none;
  color: black;
}
.article p:hover {
  cursor: pointer;

  background: linear-gradient(130deg, red 33%, rgba(0, 0, 0, 0) 33%),
    linear-gradient(130deg, white 66%, blue 66%);
  border: 1px solid var(--primary-clr-04);
  outline: 4px solid var(--primary-clr-02);
}

h2 {
  padding: 20px;
}

.gallery-container {
  display: flex;
  overflow: hidden;
  justify-content: center;
}

div.gallery {
  margin: 10px;
  padding: 20px 0;
  border: 1px solid #ccc;
  float: left;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

div.gallery:hover {
  box-shadow: 10px 10px 5px var(--primary-clr-02);
  cursor: pointer;
}

div.gallery img {
  width: 100%;
  height: auto;
}

div.desc {
  font-size: 14px;
  padding: 12px;
}
.desc p::first-letter {
  color: red;
  font-size: 2em;
  border: 1px solid;
  padding: 1px;
  margin-right: 6px;
}

form {
  max-width: 600px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  border: 1.5px solid var(--primary-clr-06);
  margin: 30px auto;
  padding: 20px;
  background-image: var(--grad-clr-03);
  color: var(--primary-clr-04);
  outline: 8px solid var(--primary-clr-03);
  box-shadow: var(--grad-clr-02);
}
.form h4 {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 10px;
  font-size: 1em;
  margin-bottom: 0;
}

.red {
  color: red;
}
.white {
  color: white;
}
.blue {
  color: blue;
}
#role,
.form input {
  padding: 8px;
  margin: 4px 0;
  border: none;
  border-bottom: 3px solid var(--primary-clr-04);
}
#role:hover {
  background-color: var(--primary-clr-03);
}

#current option:nth-child(odd) {
  background-color: rgb(142, 165, 245);
}
.keuze {
  margin: 0;
  text-align: left;
}

.form input:hover {
  background-color: var(--primary-clr-02);
}
.bet {
  margin-bottom: 0;
  text-align: left;
}
.betreft {
  margin-left: 2em;
  padding: 0.5em 0;
  align-items: flex-start;
  font-size: 12px;
}

form p {
  font-size: 12px;
  text-align: center;
}
p span {
  color: red;
}
input[type="radio"] {
  margin-left: 10px;
}
.submit {
  padding: 8px;
  margin: 20px;
  background-color: var(--primary-clr-04);
  color: var(--primary-clr-06);
  border-radius: 25px;
  border: 0.75px solid var(--primary-clr-06);
  outline: 4px solid var(--primary-clr-04);
  box-shadow: var(--grad-clr-02);
}

.submit:hover {
  cursor: pointer;
  border: none;
  color: var(--primary-clr-04);
  background-color: var(--primary-clr-07);
}

.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}

